import axios from "src/utils/axios";
import userService from "src/services/userService";
export function createUserRequest(payload, onSuccess, onError) {
  return async (dispatch) => {
    try {
      const user = await userService.create(payload);
      onSuccess(user);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}
export function getUserRequest(onSuccess, onError) {
  return async (dispatch) => {
    try {
      const list = await userService.getList();
      onSuccess(list.data);
    } catch (error) {
      onError(error);
      throw error;
    }
  };
}

